div.indentedBox {
    height: 275px;
    width: 200px;
    background: #e6e6e6; /* kolor tła boxu - najlepiej ustawić ciemniejszy od tła (lepszy efekt) */
    box-shadow: inset 2px 2px 10px 0px rgb(41, 41, 41); /* cień do środka */
    border-bottom: 1px solid #4b4b4b; /* obramowanie dolne */
    border-right: 1px solid #4b4b4b; /* obramowanie prawe */
    border-radius: 25px;
    margin-left: 30px;
}
